<template>
  <div class="date-picker-control">
    <VButton
      @click="onPrevClick"
      class="date-picker-control__button date-picker-control__button-prev"
    >
      <ArrowIcon color="#ffffff" />
    </VButton>

    <VTypography font-weight="medium" class="date-picker-control__title">
      {{ title }}
    </VTypography>

    <VButton @click="onNextClick" class="date-picker-control__button">
      <ArrowIcon color="#ffffff" />
    </VButton>
  </div>
</template>

<script>
import VButton from "@/components/buttons/VButton";
import ArrowIcon from "@/components/icons/ArrowIcon";
import VTypography from "@/components/ui/Typography";
import { DateTime } from "luxon";

export default {
  name: "DatePickerControl",
  components: { VTypography, ArrowIcon, VButton },
  emits: {
    change: () => true,
  },
  props: {
    date: DateTime,
  },
  computed: {
    title() {
      return this.date.setLocale("ru").toFormat("LLLL yyyy");
    },
  },
  methods: {
    onPrevClick() {
      this.$emit("change", this.date.minus({ month: 1 }));
    },
    onNextClick() {
      this.$emit("change", this.date.plus({ month: 1 }));
    },
  },
};
</script>

