<template>
  <div class="date-picker-days">
    <div
      class="date-picker-days__row"
      v-for="(row, rowIndex) in rows"
      :key="rowIndex"
    >
      <VButton
        @click="onDayClick(day)"
        class="date-picker-days__cell"
        v-for="(day, cellIndex) in row"
        :class="{
          'date-picker-days__cell--active': day === activeDay,
          'date-picker-days__cell--inactive': !day,
        }"
        :key="cellIndex"
      >
        {{ day }}
      </VButton>
    </div>
  </div>
</template>

<script>
import { DateTime } from "luxon";
import VButton from "@/components/buttons/VButton";

export default {
  name: "DatePickerDays",
  components: { VButton },
  emits: {
    select: () => true,
  },
  props: {
    date: DateTime,
  },
  methods: {
    onDayClick(day) {
      if (day) {
        this.$emit("select", this.date.set({ day }));
      }
    },
  },
  computed: {
    activeDay() {
      return this.date.day;
    },
    rows() {
      const monthDays = this.date.daysInMonth;
      const firstDayWeekday = this.date.set({ day: 1 }).weekday;

      const firstWeekDaysCount = 7 - firstDayWeekday + 1;
      const rowsCount = Math.ceil((monthDays - firstWeekDaysCount) / 7);
      const lastWeekDaysCount =
        monthDays - ((rowsCount - 1) * 7 + firstWeekDaysCount);

      const firstRow = Array(7).fill(undefined);
      const lastRow = Array(7)
        .fill(undefined)
        .map((_, idx) => {
          if (idx < lastWeekDaysCount) {
            return monthDays - lastWeekDaysCount + idx + 1;
          }
        });

      firstRow.splice(
        7 - firstWeekDaysCount,
        firstWeekDaysCount,
        ...Array(firstWeekDaysCount)
          .fill(0)
          .map((_, idx) => idx + 1)
      );

      const rows = [firstRow];

      const lastDayFirstWeek = firstRow[6];

      for (let i = 0; i < rowsCount - 1; i++) {
        const firstDay = lastDayFirstWeek + i * 7 + 1;
        rows.push(
          Array(7)
            .fill(0)
            .map((_, idx) => firstDay + idx)
        );
      }

      rows.push(lastRow);

      return rows;
    },
  },
};
</script>

