<template>
  <FormsContainer class="general-form">
    <VTypography
      font-weight="heavy"
      font-style="header-1"
      class="general-form__header"
      >Общая информация</VTypography
    >

    <div class="general-form__row">
      <div class="general-form__col">
        <TextInput
          label="Направлено"
          :disabled="!editable"
          placeholder="Русский текст"
          v-model="russian"
        >
          <template v-slot:popover>
            <PopoverContent :width="266">
              <VTypography font-weight="medium"
                >Фамилия Имя Отчество полностью</VTypography
              >
            </PopoverContent>
          </template>
        </TextInput>
      </div>
      <div class="general-form__col">
        <TextInput
          :disabled="!editable"
          v-model="english"
          placeholder="Английский текст"
        />
      </div>
    </div>
  </FormsContainer>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";

import TextInput from "@/components/ui/TextInput";
import VTypography from "@/components/ui/Typography";
import FormsContainer from "@/components/forms/FormsContainer";
import PopoverContent from "@/components/popover/PopoverContent";
import { isEditableStatus } from "@/helpers/StatusHelpers";

export default {
  name: "GeneralForm",
  components: {
    TextInput,
    VTypography,
    PopoverContent,
    FormsContainer,
  },
  methods: {
    ...mapMutations("general", [
      "changeRussianInformation",
      "changeEnglishInformation",
    ]),
  },
  computed: {
    ...mapGetters("formInfo", { formStatus: "getStatus" }),
    ...mapGetters("general", {
      russianInformation: "getRussianInformation",
      englishInformation: "getEnglishInformation",
    }),
    editable() {
      return isEditableStatus(this.formStatus);
    },
    russian: {
      get() {
        return this.russianInformation;
      },
      set(value) {
        this.changeRussianInformation(value);
      },
    },
    english: {
      get() {
        return this.englishInformation;
      },
      set(value) {
        this.changeEnglishInformation(value);
      },
    },
  },
};
</script>

<style scoped lang="scss">
.general-form__header {
  margin-bottom: 35px;
}

.general-form__row {
  display: flex;
}

.general-form__col {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;

  &:not(:last-child) {
    margin-right: 13px;
  }

  &:not(:first-child) {
    margin-left: 13px;
  }
}
</style>
