<template>
  <VModal class="author-modal" position="right" :show="show">
    <form @submit.prevent="onSubmit">
      <ModalContainer
        v-if="show"
        :title="title"
        @close="$emit('close', $event)"
      >
        <template v-slot:content>
          <AuthorsAutocomplete
            label="ФИО"
            :maxlength="100"
            input-type="contrast"
            :error="errors.russianName"
            placeholder="Русский текст"
            v-model="author.russian"
            name="author.russian"
            root-class="author-modal__field"
            @select="onSelect"
          >
            <template v-slot:popover>
              <PopoverContent :width="266" class="author-modal__popover">
                <VTypography font-weight="medium"
                  >Какой-то текст подсказки</VTypography
                >
              </PopoverContent>
            </template>
          </AuthorsAutocomplete>

          <TextInput
            :maxlength="100"
            input-type="contrast"
            :error="errors.englishName"
            v-model="author.english"
            name="author.english"
            placeholder="Английский текст"
          />
        </template>

        <template v-slot:footer>
          <ActionButton type="submit" button-type="outline">{{
            title
          }}</ActionButton>
        </template>
      </ModalContainer>
    </form>
  </VModal>
</template>

<script>
import VModal from "@/components/modals/Modal";
import TextInput from "@/components/ui/TextInput";
import ActionButton from "@/components/buttons/ActionButton";
import ModalContainer from "@/components/modals/ModalContainer";
import PopoverContent from "@/components/popover/PopoverContent";
import VTypography from "@/components/ui/Typography";
import AuthorsAutocomplete from "@/components/authors/AuthorsAutocomplete";
import { getInstituteAuthorsInitialValues } from "@/helpers/AuthorsHelpers";

export default {
  name: "AuthorModal",
  components: {
    AuthorsAutocomplete,
    VTypography,
    PopoverContent,
    TextInput,
    ActionButton,
    ModalContainer,
    VModal,
  },
  emits: {
    close: Function,
    submit: () => true,
  },
  props: {
    show: Boolean,
    initialValues: {
      type: Object,
      default: () => getInstituteAuthorsInitialValues(),
    },
  },
  data() {
    return {
      errors: {},
      authorFull: undefined,
      author: getInstituteAuthorsInitialValues(),
    };
  },
  computed: {
    title() {
      const hasRussianName = !!this.initialValues.russian;

      return hasRussianName ? "Изменить автора" : "Добавить автора";
    },
  },
  watch: {
    show(nextValue, prevValue) {
      if (nextValue && !prevValue) {
        this.author = { ...this.initialValues };
      }
    },
  },
  methods: {
    onSelect(author) {
      this.authorFull = author;
      this.author = author.name;
    },
    onSubmit(event) {
      this.errors = {};

      const hasRussianName = !!this.author.russian;
      const hasEnglishName = !!this.author.english;

      if (!hasRussianName) {
        this.errors = {
          russianName: "Введите имя на русском языке",
        };
      }

      if (!hasEnglishName) {
        this.errors = {
          ...this.errors,
          englishName: "Введите имя на английском языке",
        };
      }

      if (hasRussianName && hasEnglishName) {
        this.$emit("submit", {
          author: this.author,
          authorFull: this.authorFull,
        });
        this.author = getInstituteAuthorsInitialValues();

        event.target.reset();
      }
    },
  },
};
</script>

<style scoped lang="scss">
@import "~@/assets/styles/variables.scss";

.author-modal {
  justify-content: flex-end;
}

.author-modal__field {
  margin-bottom: 15px;
}

.author-modal__popover {
  background-color: $gray3;
}
</style>
