<template>
  <div class="authors-creative-table">
    <TypographyWithInfo
      font-weight="heavy"
      class="authors-creative-table__title"
    >
      Сведения о творческом вкладе Авторов

      <template v-slot:popover>
        <PopoverContent :width="238">
          <VTypography font-weight="medium"
            >В столбце сумма процентов по всем строкам должна составлять 100
            (сто) %</VTypography
          >
        </PopoverContent>
      </template>
    </TypographyWithInfo>

    <table
      v-if="authors.length > 0"
      class="authors-creative-table__table v-table"
    >
      <thead>
        <tr class="v-table__row v-table__header-row">
          <th class="v-table__cell authors-creative-table__header-number">№</th>
          <th class="v-table__cell" style="border-right: none">ФИО</th>
          <th
            class="v-table__cell authors-creative-table__header-contribution authors-creative-table__creative-cell"
            :class="{
              'authors-creative-table__creative-cell--fail':
                creativeInfo.sum < 100,
              'authors-creative-table__creative-cell--success':
                creativeInfo.sum === 100,
            }"
          >
            %
          </th>
          <th class="v-table__cell">Должность</th>
          <th class="v-table__cell">Наименование Центра (место работы)</th>
        </tr>
      </thead>

      <tbody>
        <tr
          :key="item.name"
          v-for="(item, index) in authors"
          class="v-table__row v-table__body-row"
        >
          <td class="v-table__cell">
            {{ index + 1 }}
          </td>
          <td class="v-table__cell" style="border-right: none">
            {{ item.name.russian }}
          </td>
          <td
            class="v-table__cell authors-creative-table__creative-cell"
            :class="{
              'authors-creative-table__creative-cell--fail':
                creativeInfo.sum < 100,
              'authors-creative-table__creative-cell--success':
                creativeInfo.sum === 100,
            }"
          >
            <BaseInput
              style="height: 22px"
              class="authors-creative-table__input"
              :value="item.creative"
              @input="onUpdateCreative(item.id, $event.target.value)"
            />
          </td>
          <td class="v-table__cell">
            {{ item.position.russian }}
          </td>
          <td class="v-table__cell">
            {{ item.workplace.russian }}
          </td>
        </tr>
        <tr style="height: 43px; border-bottom: 2px solid #f5f5f5">
          <td colspan="2" style="text-align: right; padding: 0 10px">
            <TypographyWithInfo position="left" style="display: block">
              ИТОГО

              <template v-slot:popover>
                <PopoverContent :width="238">
                  <VTypography font-weight="heavy"
                    >В столбце сумма процентов по всем строкам должна составлять
                    100 (сто) %</VTypography
                  >
                </PopoverContent>
              </template>
            </TypographyWithInfo>
          </td>
          <td
            style="text-align: center"
            class="authors-creative-table__creative-cell"
            :class="{
              'authors-creative-table__creative-cell--fail':
                creativeInfo.sum < 100,
              'authors-creative-table__creative-cell--success':
                creativeInfo.sum === 100,
            }"
          >
            {{ creativeInfo.sum }}
          </td>
          <td colspan="2" style="padding: 0 10px">
            <VTypography
              v-if="creativeInfo.show"
              font-weight="medium"
              style="color: #e63c2d"
              >Должно быть 100%</VTypography
            >
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import PopoverContent from "@/components/popover/PopoverContent";
import TypographyWithInfo from "@/components/ui/TypographyWithInfo";
import { mapGetters, mapMutations } from "vuex";
import BaseInput from "@/components/ui/BaseInput";
import VTypography from "@/components/ui/Typography";

export default {
  name: "AuthorsCreativeTable",
  components: { VTypography, BaseInput, PopoverContent, TypographyWithInfo },
  computed: {
    ...mapGetters("authors", ["getInstituteAuthors", "getOtherAuthors"]),
    authors() {
      return [...this.getInstituteAuthors, ...this.getOtherAuthors];
    },
    creativeInfo() {
      let sum = 0;
      let allMoreZero = true;

      this.authors.forEach((x) => {
        if (x.creative === 0) {
          allMoreZero = false;
        }

        sum = sum + x.creative;
      });

      return {
        sum,
        show: sum < 100 && allMoreZero,
      };
    },
  },
  methods: {
    ...mapMutations("authors", ["updateCreative"]),
    onUpdateCreative(id, value) {
      this.updateCreative({ id, value });
    },
  },
};
</script>

