<template>
  <table class="authors-table__table v-table">
    <thead>
      <tr class="v-table__row v-table__header-row">
        <th class="v-table__cell authors-table__number-cell">№</th>
        <th class="v-table__cell">ФИО</th>
        <th class="v-table__cell authors-table__header-actions">Действия</th>
      </tr>
    </thead>

    <tbody>
      <tr
        class="v-table__row v-table__body-row"
        v-for="(item, index) in list"
        :key="index"
      >
        <td class="v-table__cell authors-table__number-cell">
          {{ index + 1 }}
        </td>
        <td
          class="v-table__cell"
          :class="{
            'authors-table__name-cell--edit-hover': editIndexHover === index,
            'authors-table__name-cell--delete-hover':
              deleteIndexHover === index,
          }"
        >
          {{ item.name.russian }}
        </td>
        <td class="v-table__cell authors-table__actions-cell">
          <EditButton
            :disabled="!editable"
            @click="$emit('edit', index)"
            class="authors-table__edit-button"
            @mouseenter="editable ? (editIndexHover = index) : undefined"
            @mouseleave="editable ? (editIndexHover = undefined) : undefined"
          />
          <DeleteButton
            :disabled="!editable"
            @click="$emit('delete', index)"
            class="authors-table__delete-button"
            @mouseenter="editable ? (deleteIndexHover = index) : undefined"
            @mouseleave="editable ? (deleteIndexHover = undefined) : undefined"
          />
        </td>
      </tr>
    </tbody>
  </table>
</template>

<script>
import EditButton from "@/components/buttons/EditButton";
import DeleteButton from "@/components/buttons/DeleteButton";

export default {
  name: "AuthorsTable",
  components: { DeleteButton, EditButton },
  emits: {
    edit: Function,
    delete: Function,
  },
  props: {
    editable: Boolean,
    list: { type: Array, default: () => [] },
  },
  data() {
    return {
      editIndexHover: undefined,
      deleteIndexHover: undefined,
    };
  },
};
</script>

<style scoped lang="scss">
@import "~@/assets/styles/variables.scss";

.authors-table__number-cell {
  width: 61px;
  text-align: center;
}

.authors-table__header-actions {
  width: 100px;
}

.authors-table__actions-cell {
  display: flex;
  align-items: center;
  justify-content: center;
}

.authors-table__edit-button {
  margin-right: 8px;
}

.authors-table__name-cell--edit-hover {
  color: $blue;
}

.authors-table__name-cell--delete-hover {
  color: $red;
}

.authors-table__delete-button {
  margin-left: 8px;
}
</style>
