<template>
  <table class="resources-external-table__table v-table">
    <thead>
      <tr class="v-table__row v-table__header-row">
        <th class="v-table__cell resources-external-table__number-cell">№</th>
        <th class="v-table__cell">ФИО</th>
        <th class="v-table__cell resources-external-table__header-actions">
          Действия
        </th>
      </tr>
    </thead>

    <tbody>
      <tr
        class="v-table__row v-table__body-row"
        v-for="(item, index) in list"
        :key="index"
      >
        <td class="v-table__cell resources-external-table__number-cell">
          {{ index + 1 }}
        </td>
        <td
          class="v-table__cell"
          :class="{
            'resources-external-table__name-cell--edit-hover':
              editIndexHover === index,
            'resources-external-table__name-cell--delete-hover':
              deleteIndexHover === index,
          }"
        >
          {{ item.name.russian }}
        </td>
        <td class="v-table__cell resources-external-table__actions-cell">
          <EditButton
            :disabled="!editable"
            @click="$emit('edit', index)"
            class="resources-external-table__edit-button"
            @mouseenter="editable ? (editIndexHover = index) : undefined"
            @mouseleave="editable ? (editIndexHover = undefined) : undefined"
          />
          <DeleteButton
            :disabled="!editable"
            @click="$emit('delete', index)"
            class="resources-external-table__delete-button"
            @mouseenter="editable ? (deleteIndexHover = index) : undefined"
            @mouseleave="editable ? (deleteIndexHover = undefined) : undefined"
          />
        </td>
      </tr>
    </tbody>
  </table>
</template>

<script>
import DeleteButton from "@/components/buttons/DeleteButton";
import EditButton from "@/components/buttons/EditButton";

export default {
  name: "ResourcesExternalTable",
  components: { DeleteButton, EditButton },
  emits: {
    edit: Function,
    delete: Function,
  },
  props: {
    editable: Boolean,
    list: { type: Array, default: () => [] },
  },
  data() {
    return {
      editIndexHover: undefined,
      deleteIndexHover: undefined,
    };
  },
};
</script>

<style scoped></style>
