<template>
  <div class="institute-authors">
    <TypographyWithInfo font-weight="heavy" class="institute-authors__title">
      Авторы Института

      <template v-slot:popover>
        <PopoverContent :width="529">
          <VTypography font-weight="medium"
            >Просим обратить внимание, что автор — это человек, творческим
            трудом которого была создана Технология. Оказание технической,
            финансовой, организационной или материальной поддержки не является
            творческим трудом и не делает лицо, оказавшее такую помощь,
            Автором.</VTypography
          >
          <VTypography font-weight="medium"
            >В случае патентования (регистрации) Технологии персональные данные
            Авторов будут предоставлены патентным поверенным (на основании
            соглашения о неразглашении) и регистрационным органам.</VTypography
          >
        </PopoverContent>
      </template>
    </TypographyWithInfo>

    <AuthorsTable
      @edit="onEdit"
      :list="authors"
      @delete="onDelete"
      :editable="editable"
      v-if="authors.length > 0"
      class="institute-authors__table"
    />

    <AddButton v-if="editable" @click="showModal = true">
      Добавить автора
    </AddButton>

    <AuthorModal
      :show="showModal"
      @submit="onSubmit"
      @close="onCloseModal"
      :initial-values="editedAuthor"
    />
  </div>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";
import VTypography from "@/components/ui/Typography";
import AddButton from "@/components/buttons/AddButton";
import AuthorModal from "@/components/authors/AuthorModal";
import { isEditableStatus } from "@/helpers/StatusHelpers";
import AuthorsTable from "@/components/authors/AuthorsTable";
import PopoverContent from "@/components/popover/PopoverContent";
import TypographyWithInfo from "@/components/ui/TypographyWithInfo";

export default {
  name: "InstituteAuthors",
  components: {
    AuthorsTable,
    VTypography,
    PopoverContent,
    AddButton,
    AuthorModal,
    TypographyWithInfo,
  },
  emits: {
    success: Function,
  },
  data() {
    return {
      showModal: false,
      editedAuthor: undefined,
      editedAuthorIndex: undefined,
    };
  },
  computed: {
    ...mapGetters("formInfo", {
      formStatus: "getStatus",
    }),
    ...mapGetters("authors", {
      authors: "getInstituteAuthors",
    }),
    editable() {
      return isEditableStatus(this.formStatus);
    },
  },
  methods: {
    ...mapMutations("authors", [
      "addInstituteAuthor",
      "removeInstituteAuthor",
      "updateInstituteAuthor",
    ]),
    onSubmit({ author, authorFull }) {
      if (this.editedAuthorIndex >= 0) {
        this.updateInstituteAuthor({
          index: this.editedAuthorIndex,
          author: {
            ...this.editedAuthor,
            ...authorFull,
            name: author,
          },
        });
      } else {
        this.addInstituteAuthor({
          ...this.editedAuthor,
          ...authorFull,
          name: author,
        });
      }

      this.showModal = false;
      this.editedAuthor = undefined;
      this.editedAuthorIndex = undefined;

      this.$emit("success");
    },
    onEdit(index) {
      this.editedAuthorIndex = index;
      this.editedAuthor = this.authors[index].name;
      this.showModal = true;
    },
    onDelete(index) {
      this.removeInstituteAuthor(index);
    },
    onCloseModal() {
      this.showModal = false;
      this.editedAuthor = undefined;
      this.editedAuthorIndex = undefined;
    },
  },
};
</script>

<style scoped lang="scss">
.institute-authors {
  display: flex;
  flex-direction: column;
}

.institute-authors__table {
  margin-bottom: 35px;
}

.institute-authors__title {
  margin-bottom: 35px;
}
</style>
