<template>
  <div class="forms-header">
    <div class="forms-header__file-name-container">
      <FormName :title="name" />

      <VStatus :status="status" class="forms-header__status" />

      <FormSaved :date="savedAt" :loading="saving" />
    </div>

    <FormTypeSwitch v-model="type" />
  </div>
</template>

<script>
import { mapActions, mapGetters, mapMutations } from "vuex";

import FormName from "@/components/forms/FormName";
import VStatus from "@/components/forms/FormsStatus";
import FormSaved from "@/components/forms/FormSaved";
import FormTypeSwitch from "@/components/forms/FormTypeSwitch";

export default {
  name: "FormsHeader",
  components: { FormSaved, FormTypeSwitch, VStatus, FormName },
  props: {
    formId: [Number, String],
  },
  data() {
    return {
      saving: false,
      interval: undefined,
    };
  },
  methods: {
    ...mapActions("formInfo", ["saveForms"]),
    ...mapMutations("formInfo", ["changeFormType"]),
  },
  computed: {
    ...mapGetters("formInfo", {
      name: "getName",
      status: "getStatus",
      savedAt: "getSavedAt",
    }),
    type: {
      get() {
        return this.$store.state.formInfo.type;
      },
      set(type) {
        this.saveForms();
        this.changeFormType(type);
      },
    },
  },
  mounted() {
    this.interval = setInterval(() => {
      this.saving = true;

      this.saveForms().finally(() => {
        this.saving = false;
      });
    }, 60000);
  },
  beforeUnmount() {
    if (this.interval) {
      clearInterval(this.interval);
    }
  },
};
</script>

<style scoped lang="scss">
.forms-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.forms-header__file-name-container {
  display: flex;
  align-items: center;
}

.forms-header__status {
  margin-right: 70px;
}
</style>
