<template>
  <teleport to="body">
    <transition :name="name">
      <div
        v-if="show"
        role="dialog"
        v-bind="$attrs"
        class="v-modal"
        aria-modal="true"
        :class="rootClasses"
        aria-labelledby="modal-headline"
      >
        <slot></slot>
      </div>
    </transition>
  </teleport>
</template>

<script>
export default {
  name: "VModal",
  inheritAttrs: false,
  props: {
    show: Boolean,
    position: {
      type: String,
      default: "center",
      validator(value) {
        return ["left", "center", "right"].includes(value);
      },
    },
    animation: {
      type: String,
      default: "slide-right",
      validator(value) {
        return ["slide-left", "fade", "slide-right"].includes(value);
      },
    },
  },
  computed: {
    rootClasses() {
      return {
        "v-modal--left": this.position === "left",
        "v-modal--right": this.position === "right",
        "v-modal--center": this.position === "center",
      };
    },
    name() {
      switch (this.animation) {
        case "slide-left":
          return "v-model__slide-left";
        case "slide-right":
          return "v-model__slide-right";

        default:
          return "v-model__center";
      }
    },
  },
  watch: {
    show(value) {
      if (value) {
        document.body.style = "overflow:hidden";
      } else {
        document.body.style = "";
      }
    },
  },
};
</script>

<style scoped lang="scss">
@import "~@/assets/styles/mixins.scss";

.v-modal {
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  position: fixed;
  align-items: center;
}

.v-modal--center {
  justify-content: center;
}

.v-modal--left {
  justify-content: flex-start;
}

.v-modal--right {
  justify-content: flex-end;
}

.v-model__slide-right-enter-active,
.v-model__slide-right-leave-active {
  @include transition(transform);
}

.v-model__slide-right-enter-from,
.v-model__slide-right-leave-to {
  transform: translateX(800px);
}

.v-model__slide-left-enter-active,
.v-model__slide-left-leave-active {
  @include transition(transform);
}

.v-model__slide-left-enter-from,
.v-model__slide-left-leave-to {
  transform: translateX(-800px);
}

.v-model__center-enter-active,
.v-model__center-leave-active {
  @include transition(opacity);
}

.v-model__center-enter-from,
.v-model__center-leave-to {
  opacity: 0;
}
</style>
