<template>
  <div class="file-picker">
    <VButton class="file-picker__button" v-bind="$attrs">
      <VTypography
        v-if="!loading"
        class="file-picker__title"
        font-weight="medium"
        >Загрузить файлы</VTypography
      >

      <VLoader v-if="loading" :size="16" />

      <input
        type="file"
        role="button"
        :multiple="true"
        :disabled="loading"
        class="file-picker__file-input"
        @change="$emit('change', $event.target.files)"
        accept=".doc,.docx,.pdf,.xls,.xlsx,.zip,.rar,.jpg"
      />
    </VButton>

    <div class="file-picker__info-container">
      <VTypography font-weight="medium" class="file-picker__info"
        >Ограничения по размеру: 100 мб</VTypography
      >
      <VTypography font-weight="medium" class="file-picker__info"
        >Форматы файла: doc, docx, pdf, xls, xlsx, zip, rar, jpg,
        png</VTypography
      >
    </div>
  </div>
</template>

<script>
import VButton from "@/components/buttons/VButton";
import VTypography from "@/components/ui/Typography";
import VLoader from "@/components/ui/Loader";

export default {
  inheritAttrs: false,
  name: "FilePicker",
  components: { VLoader, VTypography, VButton },
  emits: {
    change: () => true,
  },
  props: {
    loading: Boolean,
  },
};
</script>

