<template>
  <VModal class="resources-external-modal" position="right" :show="show">
    <form @submit.prevent="onSubmit">
      <ModalContainer
        v-if="show"
        :title="title"
        @close="$emit('close', $event)"
      >
        <template v-slot:content>
          <TextInput
            label="Название проекта"
            :maxlength="100"
            input-type="contrast"
            :error="errors.russianName"
            placeholder="Русский текст"
            v-model="author.russian"
            name="author.russian"
            root-class="resources-external-modal__field"
            @select="onSelect"
          >
            <template v-slot:popover>
              <PopoverContent
                :width="266"
                class="resources-external-modal__popover"
              >
                <VTypography font-weight="medium"
                  >Какой-то текст подсказки</VTypography
                >
              </PopoverContent>
            </template>
          </TextInput>

          <TextInput
            :maxlength="100"
            input-type="contrast"
            :error="errors.englishName"
            v-model="author.english"
            name="author.english"
            placeholder="Английский текст"
            root-class="resources-external-modal__last-field"
          />

          <TextInput
            label="Номер проекта"
            :maxlength="100"
            input-type="contrast"
            :error="errors.russianName"
            v-model="author.russian"
            name="author.russian"
            root-class="resources-external-modal__field resources-external-modal__project-number resources-external-modal__last-field"
            @select="onSelect"
          >
            <template v-slot:popover>
              <PopoverContent
                :width="266"
                class="resources-external-modal__popover"
              >
                <VTypography font-weight="medium"
                  >Какой-то текст подсказки</VTypography
                >
              </PopoverContent>
            </template>
          </TextInput>

          <TypographyWithInfo font-weight="medium">
            Дата договора

            <template v-slot:popover>
              <PopoverContent
                :width="266"
                class="resources-external-modal__popover"
              >
                <VTypography font-weight="medium"
                  >Какой-то текст подсказки</VTypography
                >
              </PopoverContent>
            </template>
          </TypographyWithInfo>

          <DatePicker class="resources-external-modal__last-field" />

          <TextInput
            label="Номер договора"
            :maxlength="100"
            input-type="contrast"
            :error="errors.russianName"
            v-model="author.russian"
            name="author.russian"
            root-class="resources-external-modal__field resources-external-modal__project-number resources-external-modal__last-field"
            @select="onSelect"
          >
            <template v-slot:popover>
              <PopoverContent
                :width="266"
                class="resources-external-modal__popover"
              >
                <VTypography font-weight="medium"
                  >Какой-то текст подсказки</VTypography
                >
              </PopoverContent>
            </template>
          </TextInput>

          <TextInput
            label="Полное наименование лица"
            :maxlength="100"
            input-type="contrast"
            :error="errors.russianName"
            placeholder="Русский текст"
            v-model="author.russian"
            name="author.russian"
            root-class="resources-external-modal__field"
            @select="onSelect"
          >
            <template v-slot:popover>
              <PopoverContent
                :width="266"
                class="resources-external-modal__popover"
              >
                <VTypography font-weight="medium"
                  >Какой-то текст подсказки</VTypography
                >
              </PopoverContent>
            </template>
          </TextInput>

          <TextInput
            :maxlength="100"
            input-type="contrast"
            :error="errors.englishName"
            v-model="author.english"
            name="author.english"
            placeholder="Английский текст"
            root-class="resources-external-modal__last-field"
          />
        </template>

        <template v-slot:footer>
          <ActionButton type="submit" button-type="outline">{{
            title
          }}</ActionButton>
        </template>
      </ModalContainer>
    </form>
  </VModal>
</template>

<script>
import VModal from "@/components/modals/Modal";
import TextInput from "@/components/ui/TextInput";
import ActionButton from "@/components/buttons/ActionButton";
import ModalContainer from "@/components/modals/ModalContainer";
import PopoverContent from "@/components/popover/PopoverContent";
import VTypography from "@/components/ui/Typography";
import { getInstituteAuthorsInitialValues } from "@/helpers/AuthorsHelpers";
import TypographyWithInfo from "@/components/ui/TypographyWithInfo";
import DatePicker from "@/components/date-picker/DatePicker";

export default {
  name: "ResourcesExternalModal",
  components: {
    DatePicker,
    TypographyWithInfo,
    VTypography,
    PopoverContent,
    TextInput,
    ActionButton,
    ModalContainer,
    VModal,
  },
  emits: {
    close: Function,
    submit: () => true,
  },
  props: {
    show: Boolean,
    initialValues: {
      type: Object,
      default: () => getInstituteAuthorsInitialValues(),
    },
  },
  data() {
    return {
      errors: {},
      authorFull: undefined,
      author: getInstituteAuthorsInitialValues(),
    };
  },
  computed: {
    title() {
      const hasRussianName = !!this.initialValues.russian;

      return hasRussianName ? "Изменить источник" : "Добавить источник";
    },
  },
  watch: {
    show(nextValue, prevValue) {
      if (nextValue && !prevValue) {
        this.author = { ...this.initialValues };
      }
    },
  },
  methods: {
    onSelect(author) {
      this.authorFull = author;
      this.author = author.name;
    },
    onSubmit(event) {
      this.errors = {};

      const hasRussianName = !!this.author.russian;
      const hasEnglishName = !!this.author.english;

      if (!hasRussianName) {
        this.errors = {
          russianName: "Введите имя на русском языке",
        };
      }

      if (!hasEnglishName) {
        this.errors = {
          ...this.errors,
          englishName: "Введите имя на английском языке",
        };
      }

      if (hasRussianName && hasEnglishName) {
        this.$emit("submit", {
          author: this.author,
          authorFull: this.authorFull,
        });
        this.author = getInstituteAuthorsInitialValues();

        event.target.reset();
      }
    },
  },
};
</script>

