@import "~@/assets/styles/variables.scss";

.resources-external-modal {
  justify-content: flex-end;
}

.resources-external-modal__field {
  margin-bottom: 15px;
}

.resources-external-modal__popover {
  background-color: $gray3;
}

.resources-external-modal__project-number {
  width: 169px;
}

.resources-external-modal__last-field {
  margin-bottom: 35px;
}
</style>
