<template>
  <div class="disclosure-before-submission">
    <TypographyWithInfo
      font-weight="heavy"
      class="disclosure-before-submission__header"
    >
      Информация о раскрытии Технологии до даты подачи настоящей Формы раскрытия

      <template v-slot:popover>
        <PopoverContent :width="350">
          <VTypography font-weight="medium">Подсказка</VTypography>
        </PopoverContent>
      </template>
    </TypographyWithInfo>

    <TypographyWithInfo
      font-weight="medium"
      class="disclosure-before-submission__release-before-label"
    >
      Было ли описание Технологии (частично или полностью) ранее опубликовано?

      <template v-slot:popover>
        <PopoverContent :width="489">
          <VTypography font-weight="medium"
            >Просим указать, было ли описание Технологии (частично или
            полностью) ранее опубликовано или направлено для опубликования в
            таком виде, чтобы специалист в этой области мог понять и создать или
            использовать эту Технологию.</VTypography
          >
        </PopoverContent>
      </template>
    </TypographyWithInfo>

    <RadioGroup
      v-model="hasReleaseBefore"
      class="disclosure-before-submission__release-before-radio"
      :items="[
        { key: true, title: 'Да' },
        { key: false, title: 'Нет' },
      ]"
    />

    <TypographyWithInfo
      font-weight="medium"
      v-if="hasReleaseBefore"
      class="disclosure-before-submission__field-title"
    >
      Дата самого раннего раскрытия

      <template v-slot:popover>
        <PopoverContent :width="440">
          <VTypography font-weight="medium"
            >Дата начала практических работ над созданием
            Технологии.</VTypography
          >
        </PopoverContent>
      </template>
    </TypographyWithInfo>

    <DatePicker
      v-if="hasReleaseBefore"
      v-model="releaseBeforeDate"
      class="disclosure-before-submission__release-date-field"
    />

    <div class="disclosure-before-submission__row" v-if="hasReleaseBefore">
      <div class="disclosure-before-submission__cell">
        <TextInput
          v-model="releaseBeforeRussianInformation"
          label="Сведения о самом раннем раскрытии"
          placeholder="Русский текст"
        >
          <template v-slot:popover>
            <PopoverContent :width="440">
              <VTypography font-weight="medium"
                >В случае, если ответ «Да», просим предоставить сведения о самом
                раннем раскрытии: название публикации и источника, дату
                опубликования, ссылку на публикацию.</VTypography
              >
            </PopoverContent>
          </template>
        </TextInput>
      </div>
      <div class="disclosure-before-submission__cell">
        <TextInput
          v-model="releaseBeforeEnglishInformation"
          placeholder="Английский текст"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";

import TextInput from "@/components/ui/TextInput";
import RadioGroup from "@/components/ui/RadioGroup";
import DatePicker from "@/components/date-picker/DatePicker";
import PopoverContent from "@/components/popover/PopoverContent";
import TypographyWithInfo from "@/components/ui/TypographyWithInfo";

export default {
  name: "DisclosureBeforeSubmission",
  components: {
    TextInput,
    DatePicker,
    RadioGroup,
    PopoverContent,
    TypographyWithInfo,
  },
  computed: {
    ...mapGetters("disclosure", [
      "getHasReleaseBefore",
      "getReleaseBeforeDate",
      "getReleaseBeforeRussianInformation",
      "getReleaseBeforeEnglishInformation",
    ]),
    hasReleaseBefore: {
      get() {
        return this.getHasReleaseBefore;
      },
      set(value) {
        this.changeHasReleaseBefore(value);
      },
    },
    releaseBeforeDate: {
      get() {
        return this.getReleaseBeforeDate;
      },
      set(value) {
        this.changeReleaseBeforeDate(value);
      },
    },
    releaseBeforeRussianInformation: {
      get() {
        return this.getReleaseBeforeRussianInformation;
      },
      set(value) {
        this.changeReleaseBeforeRussianInformation(value);
      },
    },
    releaseBeforeEnglishInformation: {
      get() {
        return this.getReleaseBeforeEnglishInformation;
      },
      set(value) {
        this.changeReleaseBeforeEnglishInformation(value);
      },
    },
  },
  methods: {
    ...mapMutations("disclosure", [
      "changeHasReleaseBefore",
      "changeReleaseBeforeDate",
      "changeReleaseBeforeRussianInformation",
      "changeReleaseBeforeEnglishInformation",
    ]),
  },
};
</script>

