<template>
  <div
    class="date-picker"
    v-click-outside="onHideCalendar"
    @click="onHideCalendar"
  >
    <div class="date-picker__container">
      <BaseInput
        :value="day"
        class="date-picker__input"
        ref="dayInput"
        @input="day = $event.target.value"
        @focus="onFocus"
        maxlength="2"
        :class="{ 'date-picker__input--placeholder': day === '00' }"
        @blur="onBlur($event, 'day')"
        @keydown="onKeyDown($event, 'day')"
        @keyup="onKeyUp($event, 'day')"
      />
      <VTypography>.</VTypography>
      <BaseInput
        :value="month"
        maxlength="2"
        ref="monthInput"
        @focus="onFocus"
        @input="month = $event.target.value"
        class="date-picker__input"
        :class="{ 'date-picker__input--placeholder': month === '00' }"
        @blur="onBlur($event, 'month')"
        @keydown="onKeyDown($event, 'month')"
        @keyup="onKeyUp($event, 'month')"
      />
      <VTypography>.</VTypography>
      <BaseInput
        :value="year"
        maxlength="4"
        @focus="onFocus"
        ref="yearInput"
        @input="year = $event.target.value"
        :class="{ 'date-picker__input--placeholder': year === '0000' }"
        class="date-picker__input date-picker__input-year"
        @blur="onBlur($event, 'year')"
        @keydown="onKeyDown($event, 'year')"
        @keyup="onKeyUp($event, 'year')"
      />
    </div>

    <VButton @click.prevent="onShowCalendar">
      <CalendarIcon />
    </VButton>

    <DatePickerCalendar
      :date="dateTime"
      v-if="showCalendar"
      @select="onSelectDate"
      @close="onHideCalendar"
    />
  </div>
</template>

<script>
import { DateTime } from "luxon";

import VButton from "@/components/buttons/VButton";
import BaseInput from "@/components/ui/BaseInput";
import VTypography from "@/components/ui/Typography";
import CalendarIcon from "@/components/icons/CalendarIcon";
import DatePickerCalendar from "@/components/date-picker/DatePickerCalendar";

export default {
  name: "DatePicker",
  components: {
    DatePickerCalendar,
    VTypography,
    BaseInput,
    VButton,
    CalendarIcon,
  },
  emits: {
    change: () => true,
  },
  props: {
    modelValue: String,
  },
  data() {
    const baseData = {
      showCalendar: false,
    };

    if (this.modelValue) {
      const dateTime = DateTime.fromISO(this.modelValue);

      return {
        day: dateTime.day < 10 ? `0${dateTime.day}` : `${dateTime.day}`,
        year: `${dateTime.year}`,
        month: dateTime.month < 10 ? `0${dateTime.month}` : `${dateTime.month}`,
        ...baseData,
      };
    }

    return {
      day: "00",
      month: "00",
      year: "0000",
      ...baseData,
    };
  },
  computed: {
    dateTime() {
      const date = DateTime.now();

      if (
        Number(this.day) > 0 &&
        Number(this.month) > 0 &&
        Number(this.year) > 0
      ) {
        return date.set({
          day: this.day,
          year: this.year,
          month: this.month,
        });
      }

      return date;
    },
  },
  methods: {
    onShowCalendar(event) {
      event.stopPropagation();

      this.showCalendar = true;
    },
    onHideCalendar() {
      this.showCalendar = false;
    },
    onSelectDate(date) {
      this.day = date.day < 10 ? `0${date.day}` : `${date.day}`;
      this.year = `${date.year}`;
      this.month = date.month < 10 ? `0${date.month}` : `${date.month}`;

      this.onValidateDate();
    },
    onBlur(event, type) {
      const { value } = event.target;

      const valueNumber = Number(value);

      switch (type) {
        case "day": {
          const isSingleSign = value.length === 1;
          const isLessTen = valueNumber < 10;
          const isMore = valueNumber > 31;

          if (isMore) {
            this.day = "31";
          }

          if (isLessTen || isSingleSign) {
            this.day = value ? `0${value}` : "00";
          }

          break;
        }

        case "month": {
          const isSingleSign = value.length === 1;
          const isLessTen = valueNumber < 10;
          const isMore = valueNumber > 12;

          if (isMore) {
            this.month = "12";
          }

          if (isLessTen || isSingleSign) {
            this.month = value ? `0${value}` : "00";
          }

          break;
        }
      }
    },
    onFocus(event) {
      event.target.setSelectionRange(0, event.target.value.length);
    },
    onValidateDate() {
      const dateTime = DateTime.fromObject({
        day: this.day,
        month: this.month,
        year: this.year,
      });

      if (
        this.day !== "00" &&
        this.month !== "00" &&
        this.year !== "0000" &&
        dateTime.invalid == null
      ) {
        this.$emit("update:modelValue", dateTime.toISO());
      }
    },
    onKeyUp(event, type) {
      const valueLength = event.target.value.length;
      const isDigit = event.code.indexOf("Digit") === 0;

      if (isDigit) {
        if (type === "day") {
          if (valueLength === 2) {
            this.$refs.monthInput.$refs.input.focus();
          }
        } else if (type === "month") {
          if (valueLength === 2) {
            this.$refs.yearInput.$refs.input.focus();
          }
        }
      }

      this.onValidateDate();
    },
    onKeyDown(event) {
      const isTab = event.code === "Tab";
      const isBackspace = event.code === "Backspace";
      const isDigit = event.code.indexOf("Digit") === 0;

      if (!isBackspace && !isDigit && !isTab) {
        event.preventDefault();
      }
    },
  },
};
</script>

