<template>
  <div class="form-saved" v-if="!!time">
    <VLoader :stopped="!loading" />

    <VTypography
      font-style="text-2"
      font-weight="medium"
      class="form-saved__title"
      >Сохранено {{ time }}</VTypography
    >
  </div>
</template>

<script>
import VTypography from "@/components/ui/Typography";
import { DateTime } from "luxon";
import VLoader from "@/components/ui/Loader";

export default {
  name: "FormSaved",
  components: { VLoader, VTypography },
  props: {
    date: String,
    loading: Boolean,
  },
  computed: {
    time() {
      if (this.date) {
        return DateTime.fromISO(this.date).toLocaleString(
          DateTime.TIME_24_SIMPLE
        );
      }

      return undefined;
    },
  },
};
</script>

<style scoped lang="scss">
@import "~@/assets/styles/variables.scss";

.form-saved {
  display: flex;
  align-items: center;
}

.form-saved__title {
  color: $gray4;
  margin-left: 5px;
}
</style>
