<template>
  <div class="resource-external-source">
    <TypographyWithInfo
      font-weight="heavy"
      class="resource-external-source__title"
    >
      Внешний источник финансирования создания Технологии

      <template v-slot:popover>
        <PopoverContent :width="684">
          <VTypography font-weight="medium"
            >Укажите в таблице ниже сведения о внешних (любых, кроме Института)
            источниках финансирования. Если при создании Технологии
            финансирование из внешних источников не осуществлялось, поставьте
            отметку «Неприменимо».
          </VTypography>
          <VTypography font-weight="medium"
            >Просим обратить внимание на важность точного и полного указания
            сведений об источниках финансирования (например, о договорах, в
            рамках которых были предоставлены грант, субсидия, иное
            финансирование, о проекте, в целях реализации которого они
            предоставлены, о лицах, их предоставивших и т.п.). Эти сведения
            будут использованы для установления наличия лиц, обладающих правами
            на Технологию, а также для определения соответствия Технологии
            требованиям финансируемого проекта и применимого права.
          </VTypography>
        </PopoverContent>
      </template>
    </TypographyWithInfo>

    <RadioGroup
      v-model="usedExternalResources"
      class="resource-external-source__with-other-radio"
      :items="[
        { key: true, title: 'Применялись' },
        { key: false, title: 'Не применялись' },
      ]"
    />

    <ResourcesExternalTable
      v-if="usedExternalResources"
      :list="externalSources"
      class="resource-external-source__table"
    />

    <AddButton v-if="usedExternalResources" @click="showModal = true">
      Добавить источник
    </AddButton>

    <ResourcesExternalModal :show="showModal" @close="showModal = false" />
  </div>
</template>

<script>
import TypographyWithInfo from "@/components/ui/TypographyWithInfo";
import RadioGroup from "@/components/ui/RadioGroup";
import AddButton from "@/components/buttons/AddButton";
import PopoverContent from "@/components/popover/PopoverContent";
import { mapGetters, mapMutations } from "vuex";
import ResourcesExternalTable from "@/components/resources/ResourcesExternalTable";
import ResourcesExternalModal from "@/components/resources/ResourcesExternalModal";

export default {
  name: "ResourceExternalSource",
  components: {
    ResourcesExternalModal,
    ResourcesExternalTable,
    PopoverContent,
    AddButton,
    RadioGroup,
    TypographyWithInfo,
  },
  data() {
    return {
      showModal: false,
    };
  },
  methods: {
    ...mapMutations("resources", [
      "changeUsedExternalResources",
      "addExternalResource",
      "removeExternalResource",
      "updateExternalResource",
    ]),
  },
  computed: {
    ...mapGetters("resources", {
      resources: "getExternalResources",
    }),
    usedExternalResources: {
      get() {
        return this.$store.state.resources.usedExternalResources;
      },
      set(value) {
        this.changeUsedExternalResources(value);
      },
    },
  },
};
</script>

