<template>
  <div class="date-picker-calendar" @click="onRootClick">
    <DatePickerControl :date="dateTime" @change="onChange" />

    <DatePickerWeekDays />

    <DatePickerDays :date="dateTime" @select="onChangeDay" />
  </div>
</template>

<script>
import { DateTime } from "luxon";

import DatePickerDays from "@/components/date-picker/DatePickerDays";
import DatePickerControl from "@/components/date-picker/DatePickerControl";
import DatePickerWeekDays from "@/components/date-picker/DatePickerWeekDays";

export default {
  inheritAttrs: false,
  name: "DatePickerCalendar",
  components: { DatePickerDays, DatePickerWeekDays, DatePickerControl },
  emits: {
    close: Function,
    select: () => true,
  },
  props: {
    date: {
      type: DateTime,
      default: DateTime.now(),
    },
  },
  data() {
    return {
      dateTime: this.date,
    };
  },
  methods: {
    onRootClick(event) {
      event.stopPropagation();
    },
    onChange(date) {
      this.dateTime = date;

      this.$emit("select", date);
    },
    onChangeDay(date) {
      this.onChange(date);

      this.$emit("close");
    },
  },
};
</script>

