import { FormStatus } from "@/constants/FormConstants";

export function formatStatusTitle(status) {
  switch (status) {
    case "inspection":
      return "На проверке";
    case "revision":
      return "На доработке";
    case "accepted":
      return "Принято";
    case "signed":
      return "Подписано";
    default:
      return "Черновик";
  }
}

export function isEditableStatus(status) {
  return status === FormStatus.Draft || status === FormStatus.Revision;
}
