<template>
  <div class="other-authors">
    <TypographyWithInfo font-weight="heavy" class="other-authors__title">
      Иные Авторы

      <template v-slot:popover>
        <PopoverContent :width="529">
          <VTypography font-weight="medium"
            >Укажите в таблице сведения об Авторах, не являющихся Авторами
            Института ниже. Если в создании Технологии иные Авторы участия не
            принимали, поставьте отметку «Не привлекались»</VTypography
          >
        </PopoverContent>
      </template>
    </TypographyWithInfo>

    <RadioGroup
      v-model="withOtherAuthors"
      class="other-authors__with-other-radio"
      :items="[
        { key: true, title: 'Привлекались' },
        { key: false, title: 'Не привлекались' },
      ]"
    />

    <AuthorsTable
      @edit="onEdit"
      :list="authors"
      @delete="onDelete"
      v-if="authors.length > 0"
      class="other-authors__table"
    />

    <AddButton v-if="withOtherAuthors" @click="showModal = true">
      Добавить автора
    </AddButton>

    <OtherAuthorModal
      :show="showModal"
      @submit="onSubmit"
      @close="showModal = false"
    />
  </div>
</template>

<script>
import { mapMutations } from "vuex";
import RadioGroup from "@/components/ui/RadioGroup";
import AddButton from "@/components/buttons/AddButton";
import AuthorsTable from "@/components/authors/AuthorsTable";
import PopoverContent from "@/components/popover/PopoverContent";
import TypographyWithInfo from "@/components/ui/TypographyWithInfo";
import OtherAuthorModal from "@/components/authors/OtherAuthorModal";

export default {
  name: "OtherAuthors",
  components: {
    AuthorsTable,
    PopoverContent,
    AddButton,
    RadioGroup,
    OtherAuthorModal,
    TypographyWithInfo,
  },
  data() {
    return {
      showModal: false,
      editedAuthorIndex: undefined,
    };
  },
  computed: {
    authors() {
      return this.$store.state.authors.otherAuthors || [];
    },
    withOtherAuthors: {
      get() {
        return this.$store.state.authors.withOtherAuthors;
      },
      set(value) {
        this.changeWithOtherAuthors(value);
      },
    },
  },
  methods: {
    ...mapMutations("authors", [
      "updateOtherAuthor",
      "removeOtherAuthor",
      "changeWithOtherAuthors",
    ]),
    onEdit(index) {
      this.editedAuthorIndex = index;
      this.showModal = true;
    },
    onDelete(index) {
      this.removeAuthor(index);
    },
    onSubmit(author) {
      if (this.editedAuthorIndex >= 0) {
        this.updateAuthor({ index: this.editedAuthorIndex, author });
        this.editedAuthorIndex = undefined;
      } else {
        this.addAuthor(author);
      }

      this.$emit("success");

      this.showModal = false;
    },
  },
};
</script>

<style scoped lang="scss">
.other-authors {
  display: flex;
  margin-bottom: 100px;
  flex-direction: column;
}

.other-authors__title {
  margin-bottom: 35px;
}

.other-authors__with-other-radio {
  margin-bottom: 35px;
}
</style>
