<template>
  <div class="disclosure-third-parties">
    <TypographyWithInfo
      font-weight="heavy"
      class="disclosure-third-parties__header"
    >
      Раскрытие в устной или в письменной форме третьим лицам (не являющимся
      работниками или обучающимися Института)

      <template v-slot:popover>
        <PopoverContent :width="350">
          <VTypography font-weight="medium">Подсказка</VTypography>
        </PopoverContent>
      </template>
    </TypographyWithInfo>

    <TypographyWithInfo
      font-weight="medium"
      class="disclosure-third-parties__release-before-label"
    >
      Было ли описание Технологии (частично или полностью) ранее опубликовано?

      <template v-slot:popover>
        <PopoverContent :width="489">
          <VTypography font-weight="medium"
            >В случае, если ответ «Да», просим предоставить дату и описание
            самого раннего раскрытия, т.е. при каких обстоятельствах, на какой
            конференции / какому лицу и любую иную релевантную
            информацию.</VTypography
          >
        </PopoverContent>
      </template>
    </TypographyWithInfo>

    <RadioGroup
      v-model="hasReleaseThirdParties"
      class="disclosure-third-parties__release-before-radio"
      :items="[
        { key: true, title: 'Да' },
        { key: false, title: 'Нет' },
      ]"
    />

    <TypographyWithInfo
      font-weight="medium"
      v-if="hasReleaseThirdParties"
      class="disclosure-third-parties__field-title"
    >
      Дата самого раннего раскрытия

      <template v-slot:popover>
        <PopoverContent :width="440">
          <VTypography font-weight="medium"
            >Дата начала практических работ над созданием
            Технологии.</VTypography
          >
        </PopoverContent>
      </template>
    </TypographyWithInfo>

    <DatePicker
      v-if="hasReleaseThirdParties"
      v-model="releaseThirdPartiesDate"
      class="disclosure-third-parties__release-date-field"
    >
    </DatePicker>

    <div class="disclosure-third-parties__row" v-if="hasReleaseThirdParties">
      <div class="disclosure-third-parties__cell">
        <TextInput
          v-model="releaseThirdPartiesRussianInformation"
          label="Сведения о самом раннем раскрытии"
          placeholder="Русский текст"
        >
          <template v-slot:popover>
            <PopoverContent :width="440">
              <VTypography font-weight="medium"
                >В случае, если ответ «Да», просим предоставить сведения о самом
                раннем раскрытии: название публикации и источника, дату
                опубликования, ссылку на публикацию.</VTypography
              >
            </PopoverContent>
          </template>
        </TextInput>
      </div>
      <div class="disclosure-third-parties__cell">
        <TextInput
          v-model="releaseThirdPartiesEnglishInformation"
          placeholder="Английский текст"
        />
      </div>
    </div>
  </div>
</template>

<script>
import TypographyWithInfo from "@/components/ui/TypographyWithInfo";
import RadioGroup from "@/components/ui/RadioGroup";
import TextInput from "@/components/ui/TextInput";
import PopoverContent from "@/components/popover/PopoverContent";
import DatePicker from "@/components/date-picker/DatePicker";
import { mapGetters, mapMutations } from "vuex";

export default {
  name: "DisclosureThirdParties",
  components: {
    DatePicker,
    PopoverContent,
    TextInput,
    RadioGroup,
    TypographyWithInfo,
  },
  computed: {
    ...mapGetters("disclosure", [
      "getHasReleaseThirdParties",
      "getReleaseThirdPartiesDate",
      "getReleaseThirdPartiesRussianInformation",
      "getReleaseThirdPartiesEnglishInformation",
    ]),
    hasReleaseThirdParties: {
      get() {
        return this.getHasReleaseThirdParties;
      },
      set(value) {
        this.changeHasReleaseThirdParties(value);
      },
    },
    releaseThirdPartiesDate: {
      get() {
        return this.getReleaseThirdPartiesDate;
      },
      set(value) {
        this.changeReleaseThirdPartiesDate(value);
      },
    },
    releaseThirdPartiesRussianInformation: {
      get() {
        return this.getReleaseThirdPartiesRussianInformation;
      },
      set(value) {
        this.changeReleaseThirdPartiesRussianInformation(value);
      },
    },
    releaseThirdPartiesEnglishInformation: {
      get() {
        return this.getReleaseThirdPartiesEnglishInformation;
      },
      set(value) {
        this.changeReleaseThirdPartiesEnglishInformation(value);
      },
    },
  },
  methods: {
    ...mapMutations("disclosure", [
      "changeHasReleaseThirdParties",
      "changeReleaseThirdPartiesDate",
      "changeReleaseThirdPartiesRussianInformation",
      "changeReleaseThirdPartiesEnglishInformation",
    ]),
  },
};
</script>

